<template>
  <div>
    <h1>{{ homework.name }}</h1>
    <h1>
      {{ student.name }} - {{ assignment.updatedAt }} - {{ assignment.mark }}
    </h1>
    <v-divider></v-divider>
    <v-row>
      <v-col
        cols="6"
        v-for="(question, index) in homework.questions"
        :key="index"
      >
        <v-card raised tile>
          <v-card-title>
            <v-row class="text-left display-1 font-weight-black">
              <v-col cols="10"
                ><span class="font-weight-light">Question: </span
                >{{ question.text }}</v-col
              >
              <v-col cols="2">
                <v-textarea rows="1" no-resize outlined v-if="!question.point">
                </v-textarea>
                <span
                  class="red--text"
                  v-else
                  :class="{
                    'green--text': assignment.isCorrect[index] == 'true'
                  }"
                >
                  {{ question.point }}
                </span>
                <v-icon
                  v-if="assignment.isCorrect[index] == 'true'"
                  large
                  color="green"
                  >mdi-check-circle</v-icon
                >
                <v-icon v-else large color="red">mdi-close-circle</v-icon>
              </v-col>
            </v-row>
          </v-card-title>

          <v-card-text
            class="text-left font-weight-black display-1"
            :class="{
              'green--text': assignment.isCorrect[index] == 'true',
              'red--text': assignment.isCorrect[index] == 'false'
            }"
          >
            <div v-if="question.choices.length > 0">
              <v-radio-group v-model="question.answer">
                <v-radio
                  disabled
                  v-for="(choice, index) in question.choices"
                  :key="index"
                  :label="choice.value"
                ></v-radio>
              </v-radio-group>
              <v-divider></v-divider>
              <span class="font-weight-light">Student answer: </span>
              {{ getRadioValue(question, index) }}
            </div>

            <div v-else>
              <v-textarea
                no-resize
                disabled
                rows="1"
                auto-grow
                v-model="question.answer"
              ></v-textarea>
              <span class="font-weight-light">Student answer: </span>
              {{ assignment.answers[index] }}
            </div>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import StudentService from "../services/StudentService";
export default {
  data() {
    return {
      assignmentId: Number,
      assignment: {},
      homework: {},
      student: {}
    };
  },
  created() {
    this.assignmentId = this.$route.params.assignmentId;
    this.assignment = this.$route.params.assignment;
    this.homework = this.$route.params.homework;
    StudentService.GetStudent(this.assignment.studentId).then(student => {
      this.student = student;
    });
  },

  methods: {
    getRadioValue(question, index) {
      let answerIndex = this.assignment.answers[index];
      let choice = question.choices[answerIndex];
      return choice.value;
    }
  }
};
</script>

